
import Loader from "react-loader-spinner";
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css";

export default function Spinner() {
    return (
        <div className="spinner">
            <div>
                <Loader type="Circles"
                    color="#00BFFF"
                    height={100}
                    width={100}
                    timeout={3000} />
                Loading...
            </div>
        </div>
    );
}

